// hooks/useStaffData.ts

import { useEffect, useState } from 'react';
import { Staff } from '@/src/lib/staff/staffTypes';
import {
  getAllStaffs,
  createStaff,
  updateStaff,
  deleteStaff,
} from '@/src/lib/services/staff/staffService';

export function useStaffData(companyId: string) {
  const [staffs, setStaffs] = useState<Staff[]>([]);
  const [loading, setLoading] = useState(true);

  const fetchStaffs = async () => {
    setLoading(true);
    const data = await getAllStaffs(companyId);
    setStaffs(data);
    setLoading(false);
  };

  const handleCreate = async (staff: Omit<Staff, 'id' | 'createdAt' | 'updatedAt'>) => {
    await createStaff({ ...staff, accepted: false }); // üëà m·∫∑c ƒë·ªãnh khi t·∫°o m·ªõi l√† ch∆∞a x√°c nh·∫≠n
    fetchStaffs();
  };

  const handleUpdate = async (id: string, data: Partial<Staff>) => {
    await updateStaff(id, data);
    fetchStaffs();
  };

  const handleDelete = async (id: string) => {
    await deleteStaff(id);
    fetchStaffs();
  };

  useEffect(() => {
    if (companyId) fetchStaffs();
  }, [companyId]);

  return {
    staffs,
    loading,
    fetchStaffs,
    handleCreate,
    handleUpdate,
    handleDelete,
  };
}