// app/my-business/rental-company/staff/page.tsx
'use client';

import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import { useUser } from '@/src/context/AuthContext';
import StaffTable from '@/src/components/staff/StaffTable';
import StaffForm from '@/src/components/staff/StaffForm';
import NotificationDialog from '@/src/components/ui/NotificationDialog';
import { useStaffData } from '@/src/hooks/useStaffData';
import { useEffect, useState } from 'react';
import { Staff } from '@/src/lib/staff/staffTypes';
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';
import { getDocs, query, collection, where, doc, getDoc } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';

export default function StaffPage() {
  const { companyId, loading } = useUser();
  const [editingStaff, setEditingStaff] = useState<Staff | null>(null);
  const [showForm, setShowForm] = useState(false);
  const [stationMap, setStationMap] = useState<Record<string, string>>({});
  const [companyName, setCompanyName] = useState('');
  const [dialog, setDialog] = useState<{ open: boolean; type: 'success' | 'error' | 'confirm'; title: string; description?: string; onConfirm?: () => void }>({ open: false, type: 'success', title: '' });

  const {
    staffs,
    loading: staffLoading,
    handleCreate,
    handleUpdate,
    handleDelete,
    fetchStaffs,
  } = useStaffData(companyId || '');

  useEffect(() => {
    if (companyId) {
      fetchStaffs();
      loadCompanyName(companyId);
      loadStations(companyId);
    }
  }, [companyId]);

  const loadCompanyName = async (companyId: string) => {
    const snap = await getDoc(doc(db, 'rentalCompanies', companyId));
    if (snap.exists()) setCompanyName(snap.data().name);
  };

  const loadStations = async (companyId: string) => {
    const snapshot = await getDocs(query(collection(db, 'rentalStations'), where('companyId', '==', companyId)));
    const map: Record<string, string> = {};
    snapshot.docs.forEach((doc) => {
      const data = doc.data();
      map[doc.id] = data.name;
    });
    setStationMap(map);
  };

  const showDialog = (type: 'success' | 'error' | 'confirm', title: string, description?: string, onConfirm?: () => void) => {
    setDialog({ open: true, type, title, description, onConfirm });
  };

  const confirmDelete = async (id: string) => {
    try {
      await handleDelete(id);
      showDialog('success', 'Staff deleted');
    } catch {
      showDialog('error', 'Failed to delete staff');
    }
  };

  const handleDeleteWithConfirm = (id: string) => {
    showDialog(
      'confirm',
      'Are you sure you want to delete this staff member?',
      'This action cannot be undone.',
      () => confirmDelete(id)
    );
  };

  if (loading || staffLoading) return <div>Loading...</div>;
  if (!companyId) return <div>Unauthorized</div>;

  return (
    <div className="min-h-screen flex flex-col bg-gray-50">
      <Header />
      <main className="flex-1 px-6 py-10 space-y-10 max-w-4xl mx-auto">
        <div className="flex items-center justify-between">
          <h1 className="text-2xl font-bold text-gray-800">üë• Staff Management</h1>
          <Button onClick={() => { setEditingStaff(null); setShowForm(true); }}>+ Add Staff</Button>
        </div>

        {showForm && (
          <StaffForm
            companyId={companyId}
            editingStaff={editingStaff}
            onSave={() => { fetchStaffs(); setShowForm(false); setEditingStaff(null); }}
            onCancel={() => { setShowForm(false); setEditingStaff(null); }}
          />
        )}

        <StaffTable
          staffs={staffs}
          onEdit={(staff) => { setEditingStaff(staff); setShowForm(true); }}
          onDelete={handleDeleteWithConfirm}
          stationMap={stationMap}
          companyName={companyName}
        />
      </main>
      <Footer />
      <NotificationDialog
        open={dialog.open}
        type={dialog.type}
        title={dialog.title}
        description={dialog.description}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
        onConfirm={dialog.onConfirm}
      />
    </div>
  );
}